/* eslint-disable no-console */
import { BASE_URL } from "./../../config"

export default {
    name: "Login",
    data() {
        return {
            username: "",
            password: "",
            entities: [],
            entity: "",
        }
    },
    mounted: function () {
        this.axios.post(`${BASE_URL}/entities`).then(res => {
            if (res.data.status) {
                this.entities = res.data.entities.map((entity) => {
                    return {
                        label: entity.name.toUpperCase(),
                        value: entity.prefix
                    }
                })
            }
        })
    },
    methods: {
        login() {
            if(this.entity.value !== undefined){
                this.axios.post(`${BASE_URL}/login`, {
                    username: this.username,
                    password: this.password
                }).then(res => {
                    if (res.data.status) {
                        localStorage.setItem('access_token', res.data.access_token)
                        localStorage.setItem('entity_prefix', this.entity.value)
                        this.$router.push({name:'Dashboard'})
                    }else {
                        console.log('nope')
                        this.$q.notify({
                            position: 'top',
                            color: 'negative',
                            message: 'Usuario o contrasea incorrectos!',
                            timeout: 2000,
                        })
                    }
                })
            } else {
                this.$q.notify({
                    position: 'top',
                    color: 'warning',
                    message: 'Seleccione una entidad',
                    timeout: 2000,
                })
            }
        },
        enter(){
            if(this.username.length == 0){
                this.$refs.user.focus()
            } else if(this.password.length == 0){
                this.$refs.pass.focus()
            } else {
                this.login()
            }
        }
    }
}