/* eslint-disable no-console */
import { BASE_URL } from "./../../../config"

export default {
    name: "DashboardLayout",
    data() {
        return {
            drawer: false,
            headerState: true,
            config: '',
            miniDrawState: true,
            dashActive: true,
            userFormActive: false,
            chargeActive: false,
            listActive: false
        }
    },
    mounted: function () {
        this.routeElements(this.$router.currentRoute.name)

        this.config = {
            headers: {
                'Authorization': 'Bearer ' + localStorage.getItem('access_token')
            }
        }
    },
    methods: {
        logout() {
            this.axios.post(`${BASE_URL}/logout`, {}, this.config)
            .then(res => {
                if(res.data.status) {
                    this.$q.notify({
                        position: 'top',
                        color: 'blue-grey-7',
                        message: 'Se cerr la sesin',
                        timeout: 2000,
                    })
                }
            })

            localStorage.removeItem('access_token')
            this.$router.push({name:'Login'})
        },
        route(name) {
            if((name != 'Dashboard' && this.dashActive) || (name != 'UserForm' && this.userFormActive) ||
                (name != 'List' && this.listActive) || (name != 'ChargeList' && this.chargeActive)){
                this.routeElements(name)
                this.$router.push({name: name})
            }
        },
        routeElements(name){
            switch(name){
                case 'Dashboard':
                    this.dashActive = true
                    this.userFormActive = false
                    this.listActive = false
                    this.chargeActive = false
                    break
                case 'UserForm':
                    this.dashActive = false
                    this.userFormActive = true
                    this.listActive = false
                    this.chargeActive = false
                    break
                case 'List':
                    this.dashActive = false
                    this.userFormActive = false
                    this.listActive = true
                    this.chargeActive = false
                    break
                case 'ListForm':
                    this.dashActive = false
                    this.userFormActive = false
                    this.listActive = true
                    this.chargeActive = false
                    break
                case 'ChargeList':
                    this.dashActive = false
                    this.userFormActive = false
                    this.listActive = false
                    this.chargeActive = true
                    break
                case 'ChargeForm':
                    this.dashActive = false
                    this.userFormActive = false
                    this.listActive = false
                    this.chargeActive = true
                    break
            }
            
        }
    }
}