export default {
    name: "Dashboard",
    data() {
        return {
            calls: 70,
            progress: 5,
            finished: 65,
        }
    },
    mounted: function () {
        setInterval(() => {
            this.calls = this.calls + Math.round(Math.random() * 10)
            this.progress = this.progress + Math.round(Math.random() * 10)
            this.finished = this.finished + Math.round(Math.random() * 10)
        }, 2000)
    }
}