/* eslint-disable no-console */
import { BASE_URL } from "../../../config"

export default {
    name: "ListForm",
    data() {
        return {
            config: '',
            lists: [],
            campaigns: [],
            columns: [
                {
                    name: 'number',
                    required: true,
                    label: 'N',
                    align: 'center',
                    field: row => row.name,
                },
                {
                    name: 'name',
                    required: true,
                    label: 'NOMBRE',
                    align: 'center',
                    field: row => row.name,
                },
                {
                    name: 'id',
                    required: true,
                    label: 'ID',
                    align: 'center',
                    field: row => row.id,
                },
                {
                    name: 'campaign',
                    required: true,
                    label: 'CAMPAA',
                    align: 'center',
                    field: row => row.campaign,
                },
                {
                    name: 'status',
                    required: true,
                    label: 'ESTADO',
                    align: 'center',
                    field: row => row.status,
                },
                {
                    name: 'actions',
                    required: true,
                    label: 'ACCIONES',
                    align: 'center'
                }
            ],
            popupData: {
            },
            showDetails : false
        }
    },
    mounted: function() {
        this.config = {
            headers: {
                'Authorization': 'Bearer ' + localStorage.getItem('access_token')
            }
        }
        
        this.axios.post(`${BASE_URL}/campaigns`, {}, this.config)
            .then((response)=>{
                if(response.data.status){
                    this.campaigns = response.data.campaigns.map(campaign => {
                        return {
                            label: campaign.campaign_name,
                            value: campaign.campaign_id
                        }
                    })
                }
                this.refreshLists()
            })
    },
    methods: {
        getCampaignName(id){
            let response = this.campaigns.filter(campaign => campaign.value == id)
            if(response.length)return response[0].label
            return id
        },
        refreshLists(){
            this.axios.post(`${BASE_URL}/all-lists`, {}, this.config)
                .then((response)=>{
                    if(response.data.status){
                        this.lists = response.data.lists.map((list, index) => {
                            return {
                                id: list.list_id,
                                number: index + 1,
                                name: list.list_name,
                                campaign: this.getCampaignName(list.campaign_id),
                                status: list.active == 'Y' ? true : false,
                            }
                        })
                    }
                })
        },
        onChangeListStatus(id, status){
            status = status ? 'Y' : 'N'
            this.axios.post(`${BASE_URL}/update-list-status`, {
                list_id: id,
                status
            }, this.config)
                .then((response)=>{
                    if(!response.data.status){
                        this.$q.notify({
                            position: 'top',
                            color: 'negative',
                            message: 'Error al actualizar',
                            timeout: 2500,
                            actions: [{ icon: 'close', color: 'white' }]
                        })
                    }
                })

        },
        onShowDetails(id){
            let charge = this.charges.filter(charge => charge.id == id)
            if(charge) this.popupData = charge[0]
            this.showDetails = true
        }
    },
}