/* eslint-disable no-console */
import { BASE_URL } from "./../../../config"
import SheetExcel from "./../../SheetExcel/SheetExcel"
import Excel from 'exceljs'

export default {
    name: "ChargeForm",
    data() {
        return {
            msg: "",
            isError: false,
            iterations: 0,
            seconds: 0,
            loading: false,
            btnMatchLoad: false,
            lists: [],
            carriers: [],
            carrier: '',
            list: '',
            dialogStatus: false,
            config: '',
            dbName: '',
            report: {
                msg: 'NA',
                time: 'NA',
                clients: 'NA',
                products: 'NA',
                duplicates: 'NA',
                outs: 'NA',
                out: 'NA',
                process: 'NA',
                show: false
            },
            isReport: false,
            excelColumns: [],
            dataColumn: {
                code: {
                    field: '',
                    class: 'grey',
                    clickClass: 'blue-grey-10'
                },
                name: {
                    field: '',
                    class: 'grey',
                    clickClass: 'red'
                },
                product: {
                    field: '',
                    class: 'grey',
                    clickClass: 'orange-9'
                },
                amount: {
                    field: '',
                    class: 'grey',
                    clickClass: 'brown-8'
                },
                currency: {
                    field: '',
                    class: 'grey',
                    clickClass: 'blue-8'
                },
                date: {
                    field: '',
                    class: 'grey',
                    clickClass: 'green-8'
                },
                phone: {
                    field: '',
                    class: 'grey',
                    clickClass: 'secondary'
                },
                prevSelected: ''
            },
            dataColMatch: {},
            allowMatch: false,
            chargeLoadedClass: 'grey'
        }
    },
    mounted: function() {
        this.config = {
            headers: {
                'Authorization': 'Bearer ' + localStorage.getItem('access_token')
            }
        }

        this.dbName = localStorage.getItem('entity_prefix')

        this.axios.post(`${BASE_URL}/lists`, {}, this.config).then(res => {
            if (res.data.status) {
                this.lists = res.data.lists.map(item => {
                    return {
                        label: item.list_name,
                        value: item.list_id
                    }
                })
            }
        })

        this.axios.post(`${BASE_URL}/carriers`, {entity_prefix: this.dbName}, this.config).then(res => {
            if (res.data.status) {
                this.carriers = res.data.carriers.map(item => {
                    return {
                        label: item.name,
                        value: item.id
                    }
                })
            }
        })

    },
    methods: {
        validateSuccess(){
            let msg = ''
            if(this.$refs.uploader.files.length == 0){
                msg = "Seleccione un archivo"
            } else if(this.list.value == undefined){
                msg = "Seleccione una lista"
            } else if(this.carrier.value == undefined){
                msg = "Seleccione un proveedor"
            } else if(this.iterations < 0 || this.iterations > 10){
                msg = "Corrija el campo de iteraciones"
            } else if(this.seconds < 60 || this.seconds > 10000){
                msg = "Corrija el campo de horas"
            } else if(!this.dbName){
                msg = "Problemas internos. Intente ms tarde."
            }else{
                return {status: true}
            }

            return {status: false, msg: msg}
        },
        upload() {
            this.isReport = false
            this.loading = true
            
            this.report.msg = 'NA',
            this.report.time = 'NA',
            this.report.clients = 'NA',
            this.report.products = 'NA',
            this.report.duplicates = 'NA',
            this.report.out = 'NA',
            this.report.process = 'NA',
            this.report.show = false

            let res = this.validateSuccess()

            if (res.status) {
                let file = this.$refs.uploader.files[0]
                let data = new FormData()

                data.append("file", file)
                data.set("iterations", this.iterations)
                data.set("seconds", this.seconds)
                data.set("list_id", this.list.value)
                data.set("carrier_id", this.carrier.value)
                data.set("entity_prefix", this.dbName)
                data.set("matched_columns", JSON.stringify({
                    code : this.dataColumn.code.field,
                    name : this.dataColumn.name.field,
                    product : this.dataColumn.product.field,
                    amount : this.dataColumn.amount.field,
                    currency : this.dataColumn.currency.field,
                    date : this.dataColumn.date.field,
                    phone : this.dataColumn.phone.field
                }))

                this.axios.post(`${BASE_URL}/carga`, data, {
                    headers: {
                        'Authorization': 'Bearer ' + localStorage.getItem('access_token'),
                        "Content-Type": "multipart/form-data"
                    }
                })
                .then(res => {
                    if (res.data.status) {
                        this.isError = false
                        this.iterations = ""
                        this.seconds = ""
                        this.carrier = ""
                        this.list = ""
                        this.$refs.uploader.reset()
                        this.chargeLoadedClass = "positive"
                        this.$q.notify({
                            position: 'top',
                            color: 'positive',
                            message: 'Se cre la lista!',
                            timeout: 2000,
                        })
                        this.allowMatch = false
                    } else {
                        this.chargeLoadedClass = "negative"
                        this.$q.notify({
                            position: 'top',
                            color: 'negative',
                            message: 'Error! Verifique los datos',
                            timeout: 2500,
                            actions: [{ icon: 'close', color: 'white' }]
                        })
                    }
                    this.report.time = res.data.time != undefined ? res.data.time : 'NA'
                    this.report.clients = res.data.count_clients != undefined ? res.data.count_clients : 'NA'
                    this.report.products = res.data.count_products != undefined ? res.data.count_products : 'NA'
                    this.report.duplicates = res.data.count_duplicates != undefined ? res.data.count_duplicates : 'NA'
                    this.report.out = res.data.count_out != undefined ? res.data.count_out : 'NA'
                    this.report.msg = res.data.msg != undefined ? res.data.msg : 'NA'
                    this.report.process = res.data.process != undefined ? res.data.process : 'NA'
                    this.report.show = true
                    this.isReport = true
                })
                .then(() => {
                    this.loading = false
                    this.isReport = true
                }).catch(()=>{
                    this.loading = false
                    this.isReport = true
                })
            } else {
                this.loading = false
                this.$q.notify({
                    position: 'top',
                    color: 'warning',
                    message: res.msg != undefined ? res.msg : 'Complete los campos',
                    timeout: 2500,
                    actions: [{ icon: 'close', color: 'white' }]
                })
            }
        },
        onFileSelected(){
            this.allowMatch = true
            this.btnMatchLoad = true
            let workbook = new Excel.Workbook()
            let file = this.$refs.uploader.files[0]
            let reader = new FileReader()
            reader.onload = () => {
                workbook.xlsx.load(reader.result)
                .then(() => {
                    let sheet = undefined
                    for(let i=0; i < 10; i++){
                        sheet = workbook.getWorksheet(i)
                        if(sheet != undefined){
                            this.excelColumns = sheet.getRow(1).values
                            this.dialogStatus = true
                            break
                        }
                    }
                    this.btnMatchLoad = false
                });
            }
            reader.readAsArrayBuffer(file)
        },
        clickData(key){
            if(!this.dataColumn[key].field){
                if(!this.dataColumn.prevSelected){
                    this.dataColumn[key].class = this.dataColumn[key].clickClass
                    this.dataColumn.prevSelected = key
                }else if(this.dataColumn.prevSelected == key && !this.dataColumn[key].field){
                    this.dataColumn.prevSelected = ''
                    this.dataColumn[key].class = 'grey'
                }
            }
        },
        tryMatch(field){
            if(this.dataColumn.prevSelected){
                this.dataColumn[this.dataColumn.prevSelected].field = field
                this.$set(this.dataColMatch, field, this.dataColumn[this.dataColumn.prevSelected].class)
                this.dataColumn.prevSelected = ''
            }
        },
        resetMatcher(){
            this.dataColumn = {
                code: {
                    field: '',
                    class: 'grey',
                    clickClass: 'blue-grey-10'
                },
                name: {
                    field: '',
                    class: 'grey',
                    clickClass: 'red'
                },
                product: {
                    field: '',
                    class: 'grey',
                    clickClass: 'orange-9'
                },
                amount: {
                    field: '',
                    class: 'grey',
                    clickClass: 'brown-8'
                },
                currency: {
                    field: '',
                    class: 'grey',
                    clickClass: 'blue-8'
                },
                date: {
                    field: '',
                    class: 'grey',
                    clickClass: 'green-8'
                },
                phone: {
                    field: '',
                    class: 'grey',
                    clickClass: 'secondary'
                },
                prevSelected: ''
            }
            this.dataColMatch = {}
        }
    },
    components: {
        'SheetExcel': SheetExcel
    }
}