/* eslint-disable no-console */
import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login/Login'
import Dashboard from '@/components/Dashboard/Dashboard'
import CreateList from '@/components/List/Form/FormList'
import List from '@/components/List/List/List'
// import Updata from '@/components/Updata/Updata'
import UserForm from '@/components/UserForm/UserForm'
import ChargeList from '@/components/Charge/List/Charge'
import ChargeForm from '@/components/Charge/Form/ChargeForm'
import DashboardLayout from '@/components/Layouts/dashboard/DashboardLayout'
import axios from 'axios'
import { BASE_URL } from '../config'

Vue.use(Router)

const router = new Router({
    mode: 'history',
    routes: [
        {
            path: '/',
            name: 'Login',
            component: Login,
            meta: {
                title: 'Login'
            }
        },
        {
            path: '/dashboard',
            component: DashboardLayout,
            children: [
                {
                    path: '/dashboard',
                    name: 'Dashboard',
                    component: Dashboard,
                    meta: {
                        title: 'Dashboard'
                    }
                },
                {
                    path: '/charge',
                    name: 'ChargeList',
                    component: ChargeList,
                    meta: {
                        title: 'Cargas'
                    }
                },
                {
                    path: '/charge-form',
                    name: 'ChargeForm',
                    component: ChargeForm,
                    meta: {
                        title: 'Subir lista'
                    }
                },
                {
                    path: '/lists',
                    name: 'List',
                    component: List,
                    meta: {
                        title: 'Listas'
                    }
                },
                {
                    path: '/create-list',
                    name: 'ListForm',
                    component: CreateList,
                    meta: {
                        title: 'Crear lista'
                    }
                },
                {
                    path: '/user-form',
                    name: 'UserForm',
                    component: UserForm,
                    meta: {
                        title: 'Formulario de usuario'
                    }
                }
            ]
        },
        {
            path: '*',
            redirect: '/'
        }
    ]
})

router.beforeEach((to, from, next) => {
    document.title = to.meta.title || 'Rept'

    let def = true
    
    if(to.name != 'Login'){
        def = '/login'
    }

    axios.post(`${BASE_URL}/validate`, {}, {
        headers: {
            'Authorization': 'Bearer ' + localStorage.getItem('access_token')
        }
    }).then((res) => {
        if(res.data.status){
            if(to.name != 'Login'){
                next({query : {from:from}})
            }else{
                document.title = 'Dashboard'
                next({
                    path : '/dashboard',
                    query : {from:from}
                })
            }
        }
    }).catch(() => {
        next({
            path: def,
            query : {from:from}
        })
    })
})

export default router